import React from "react";
import { useForm } from "react-hook-form";
import "../css/login.css";

function Login() {
    const {
        register, handleSubmit, formState: { errors }
    } = useForm()
    const onsubmit = (data) => {//can write variable and functions above return
        // alert('Hello ' + Name)
        console.log(data)
    }

    return ( //xml code - similar to html
        <form className="login" onSubmit={handleSubmit(onsubmit)}>
            <br />
            {/* <h2>USER LOGIN</h2> */}
            <label><i className="fa-solid fa-user fa-xs"></i> Username</label>
            <input {...register('username', {
                required: {
                    value: true,
                    message: 'Username is required'
                },
                minLength: { value: 3, message: 'Username should have atleast 3 characters.' },
                maxLength: { value: 10, message: 'Username should not exceed 10 characters' }
            })} type="text" placeholder="Enter your username" /><br />

            <small>{errors.username && errors.username.message}</small>

            <label><i className="fa-solid fa-lock fa-xs"></i> Password</label>
            <input {...register('password', {
                required: {
                    value: true,
                    message: 'Password is required'
                },
                minLength: { value: 3, message: 'Password should have atleast 3 characters.' },
                maxLength: { value: 10, message: 'Password should not exceed 10 characters' }
            })} type="password" placeholder="Enter your password" /><br />

            <small>{errors.password && errors.password.message}</small>


            <div style={{ textAlign: 'center' }}>

                {/* <button onClick={onsubmit} type="submit" //alert will display on clicking login button
                //  style="text-align: center"
                >LOGIN</button> */}

                {/* <button onClick={onsubmit()} type="submit" //alert will display immediately after compiling
                //  style="text-align: center"
                >LOGIN</button> */}

                <button type="submit"  //standard way of calling methods
                //  style="text-align: center"
                ><i className="fa-solid fa-right-from-bracket fa-lg"></i> LOGIN</button>


            </div>
            <br />
        </form >
    )
}

export default Login;